import zbutton from './plugins/zbutton/index'
import zswitch from './plugins/zswitch/index'
import ztable from './plugins/ztable/index'
import zprogress from './plugins/zprogress/index'
import zcollapse from './plugins/zcollapse/index'
import zcalendar from './plugins/zcalenda/index'

const components = [
    zbutton,
    zswitch,
    ztable,
    zprogress,
    zcollapse,
    zcalendar
];

const install = function (Vue, opts = {}) {
    components.forEach(component => {
        Vue.component(component.name, component);
    });
    // 
    // Vue
    // eg:element-ui
    //      Vue.prototype.$message = Message;
    //      this.$message({message:"xxxxx",type:"success"});
};

if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

export default {
    version: '0.0.1',
    install,
    zbutton,
    zswitch,
    ztable,
    zprogress,
    zcollapse,
    zcalendar
};


